import React, { useState, useEffect } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, ChevronsLeft, ChevronsRight
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'

import useJwt from '@src/auth/jwt/useJwt'
import { formatReadableDate } from '../../../helper'
import DataTable from 'react-data-table-component'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { Error, Success, ErrorMessage } from '../../../viewhelper'
import { CURRENCY_SYMBOL } from '../../../Configuarables'
import { useHistory } from 'react-router-dom'
import InvoiceModal from './InvoiceModal'
import CommonDataTable from '../../CommonDataTable'

const InvoiceTable = () => {
    const history = useHistory()
    const userRole = JSON.parse(localStorage.getItem('userData')).role
    const [pay_amount, setpay_amount] = useState(0)
    const [ispay_amountLoading, setispay_amountLoading] = useState(false)
    const [FilteredInvoices, setFilteredInvoices] = useState([])
    const [InvModal, setInvModal] = useState(false)
    const [payApprvModal, setpayApprvModal] = useState(false)
    const [InvoiceDetails, setInvoiceDetails] = useState({})
    const [tableDataLoading, setTDL] = useState(true)
    const [InvoicesData, setInvoicesData] = useState([])
    const [PageCount, setPageCount] = useState(1)
    const [PageDataCount, setPageDataCount] = useState(5)
    const [showFilteredData, setshowFilteredData] = useState(false)
    const [invoice_id, setinvoice_id] = useState(null)
    const [reset, setReset] = useState(false)
    const [startDate, setstartDate] = useState('')
    const [endDate, setendDate] = useState('')

    const getAllInvoicesData = (page, limit) => {
        useJwt.GetAllInvoices({ page, limit }).then(res => {
            console.log("getAllInvoicesData", res.data.payload)
            setInvoicesData(res.data.payload)
            setTDL(false)
        }).catch(error => {
            console.log("getAllInvoicesData", error)
            setTDL(false)
            // Error(error)
        })
    }

    // const onchange = (e) => {
    //     setUserInput({ ...userInput, [e.target.name]: e.target.value })
    // }

    useEffect(() => {
        getAllInvoicesData(PageCount, PageDataCount)
    }, [reset])

    // Data table coulumn
    const column = [
        {
            name: 'Invoice ID',
            maxWidth: '120px',
            // sortable: true,
            selector: 'invoice_id'
        },
        {
            name: 'Invoices Issue Date',
            minWidth: '220px',
            // sortable: true,
            selector: row => formatReadableDate(row.issue_date)
        },
        {
            name: 'Total Amount',
            minWidth: '150px',
            // sortable: true,
            selector: row => `${CURRENCY_SYMBOL} ${row.total_amount}`
        },
        {
            name: 'Paid Amount',
            minWidth: '150px',
            // sortable: true,
            selector: row => `${CURRENCY_SYMBOL} ${row.pay_amount}`
        },
        {
            name: 'Status',
            minWidth: '180px',
            // sortable: true,
            selector: row => {
                const statusBG = {
                    2: { title: 'Paid', color: 'success' },
                    14: { title: 'Due', color: 'warning' },
                    15: { title: 'Partial pay', color: 'light-warning' },
                    16: { title: 'Pending approval', color: 'light-success' }
                }
                return <Badge color={statusBG[row.statusType.id].color} pill className='px-1'>
                    {statusBG[row.statusType.id].title}
                </Badge>
            }
        },
        {
            name: 'View',
            maxWidth: '50px',
            // sortable: true,
            selector: row => <span title="View">
                <Eye size={18}
                    color='seagreen'
                    style={{ cursor: 'pointer' }}
                    onClick={(e) => {
                        setInvModal(true)
                    }}
                />
            </span>
        },
        {
            name: 'Payment',
            minWidth: '150px',
            // sortable: true,
            selector: row => <Button.Ripple size='sm' color='primary' onClick={(e) => {
                setinvoice_id(row.invoice_id)
                setInvoiceDetails(row)
                setpayApprvModal(true)
            }}>
                <span >Approve</span>
            </Button.Ripple>
        }
    ]
    const handlePagination = (type) => {
        setTDL(true)
        if (type === 'prev') {
            setPageCount(PageCount - 1)
            getAllInvoicesData(PageCount - 1, PageDataCount)
        } else {
            setPageCount(PageCount + 1)
            getAllInvoicesData(PageCount + 1, PageDataCount)
        }
    }
    const handleFilter = e => {
        e.preventDefault()
        setTDL(true)
        useJwt.GetAllInvoicesByRange({
            startDate,
            endDate
        }).then(res => {
            console.log("GetAllInvoicesByRange", res.data.payload)
            setFilteredInvoices(res.data.payload)
            setTDL(false)
            setshowFilteredData(true)
        }).catch(error => {
            Error(error)
            setTDL(false)
        })
    }
    const handleSubmit = e => {
        e.preventDefault()
        setispay_amountLoading(true)
        console.log({ invoice_id, pay_amount })
        useJwt.ApprovePaymentByAdmin({ invoice_id, pay_amount }).then(res => {
            setispay_amountLoading(false)
            setpayApprvModal(false)
            setReset(!reset)
        }).catch(error => {
            Error(error)
            setispay_amountLoading(false)
        })
    }
    return (
        <Card>
            <CardHeader className='border-bottom titleSelect'>
                <CardTitle tag='h4'>Invoices</CardTitle>
                <Form className='d-flex' onSubmit={handleFilter}>
                    <Input required type='month' onChange={e => setstartDate(new Date(e.target.value).toJSON().slice(0, 10).split('-').reverse().join('/'))}></Input>&nbsp;
                    <Input required type='month' onChange={e => setendDate(new Date(e.target.value).toJSON().slice(0, 10).split('-').reverse().join('/'))}></Input>&nbsp;
                    <Button.Ripple size='sm' color='primary' type='submit' className='pr-1' style={{ minWidth: '80px' }}>
                        <span >Filter</span>
                    </Button.Ripple>
                </Form>
            </CardHeader>
            <CardBody>
                <Row>
                    {showFilteredData ? <Col md='12'>
                        <CommonDataTable column={column} TableData={FilteredInvoices} TableDataLoading={tableDataLoading} />
                    </Col> : <Col sm="12">
                        <DataTable
                            noHeader
                            highlightOnHover
                            data={InvoicesData}
                            columns={column}
                            className='react-dataTable'
                            progressPending={tableDataLoading}
                            sortIcon={<ChevronDown size={10} />}
                            progressComponent={<Spinner color='primary' />}
                            responsive={true}
                        />
                        <div className='text-right'>
                            <Button.Ripple size='sm' disabled={PageCount === 1} className='mt-1' color='secondary' onClick={e => handlePagination('prev')}>
                                <ChevronsLeft color='white' size={15} />
                            </Button.Ripple>&nbsp;&nbsp;&nbsp;&nbsp;<Button.Ripple size='sm' disabled={!InvoicesData.length} className='mt-1' color='secondary' onClick={e => handlePagination('next')}>
                                <ChevronsRight color='white' size={15} />
                            </Button.Ripple>
                        </div>
                    </Col>}
                </Row>
            </CardBody>
            {Object.keys(InvoiceDetails).length ? <InvoiceModal InvoiceDetails={InvoiceDetails} InvModal={InvModal} setInvModal={setInvModal} /> : ''}
            <Modal
                isOpen={payApprvModal}
                toggle={() => setpayApprvModal(!payApprvModal)}
                className={`modal-dialog-centered`}
            >
                <ModalHeader toggle={() => setpayApprvModal(!payApprvModal)}>Enter The Amount You Received</ModalHeader>
                <ModalBody>
                    <Form className="row pt-1" style={{ width: '100%' }} onSubmit={handleSubmit} autoComplete="off">
                        <Col md='12' >
                            <FormGroup>
                                <Input
                                    required
                                    type="number"
                                    min={1}
                                    id='pay_amount'
                                    name="pay_amount"
                                    placeholder="amount"
                                    value={pay_amount}
                                    onChange={e => setpay_amount(e.target.value)}
                                />
                            </FormGroup>
                        </Col>
                        <Col md="12" className='text-center'>
                            <Button.Ripple className='mt-2' color='primary' type="submit" disabled={ispay_amountLoading}>
                                {ispay_amountLoading ? <Spinner size='sm' /> : 'Submit'}
                            </Button.Ripple>
                        </Col>
                    </Form>
                </ModalBody>
            </Modal >
        </Card >
    )
}

export default InvoiceTable